.header{
    width: 100%;
    height: 70px;
    line-height: 70px;
}
.nav_container
{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.logo
{
    display: flex;
    align-items: center;
    column-gap: 1 rem;
    
}
.logo-img{
    width: 40px;
    height: 40px;
    padding: 10px;
    line-height: 40px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.logo-img img{
    width: 6rem;
}
.navigation
{
    display: flex;
    text-align: center;
    align-items: center;
}
.menu
{
    display: flex;
    align-items: center;
    column-gap: 3rem;
}
.nav_item
{
    color:brown;
    font-weight: 600 !important;
    cursor: pointer;
    transition: 0.3s;
}
.nav_item:hover
{
    color:blueviolet;
}