.footer {
  padding: 50px 0px;
  background: #eeeaff;
  width: 100%;
  margin-top: auto;
}
.footer_wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  column-gap: 2rem;
  background: #eeeaff;
}
.footer_box {
  width: 30%;
}
.logo
{
    display: flex;
    align-items: center;
    column-gap: 1 rem;
    
}
.logo_img{
    width: 40px;
    height: 40px;
    padding: 10px;
    line-height: 40px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.logo_img img{
    width: 8rem;
}
.footer_box p {
  line-height: 1.2rem;
  margin-top: 20px;
}
.footer_title {
  font-size: 1.2 rem;
  margin-bottom: 20px;
  margin-left: 35px;
}
.footer_links li {
  line-height: 30px;
  color: #04020c;
}
.footer_links li a {
  cursor: pointer;
}
.footer_links li a:hover {
  color: #7aa11f;
}
