:root{
  --primary-color:#6f55f2,
  --heading-color:#132742
}
body{
  font-family:Arial, Helvetica, sans-serif;
  font-size: medium;
}
section{
  padding: 50px 0px;
}
a{
  text-decoration: none;
  color: unset;
}
a:hover
{
  color: unset;
}
link:hover
{
  color: unset;
}
.container
{
  width: 1140px;
  padding: 0px 15px;
  margin: auto;
}
.section-title
{
  color: var(--heading-color);
  font-weight: 50px;
}
.register-btn
{
  background:#121981;
  border: none;
  border-radius: 5px;
  color: #eeefff;
  cursor: pointer;
  font-family: var(--font-name);
  font-size: 1rem;
  font-weight: 500;
  outline: none;
  padding: 7px 15px;
}
